import React from 'react';
import styled from 'styled-components';
import { Button, ButtonGroup } from 'react-bootstrap';
import { Form } from 'react-bootstrap';
import axios from 'axios';
import { useEffect, useState } from 'react';
import "./form_field.css";

const Wrapper = styled.div`
    /* 레이아웃 */
    display: flex;
    flexDirection: row;
    position: fixed;
    width: 100%;
    //max-width: 1100px;
     max-height:100%;
    // margin: 0 auto;
    // align-items: center;
    //margin-left: 280px;
    overflow-y : auto;
`;




const Write = props => {

    const [title, setTitle] = useState('');
    const [content, setContent] = useState('');
    const [image, setImage] = useState({
      image_file: "",
      preview_URL: "img/default_image.jpg",
    });
    const [Image_file, setImagefile] = useState();
    const [category, setCategory] = useState('');
    // const [published_date, setDate] = useState('');

    let inputRef;


    const onImageHandler = (event) => {
      setImage(event.target.files[0])
  }

  const CreateBoard = async({body}) => {

    const token = localStorage.getItem('token');
    const headers = {
        "Content-Type" : "application/json",
        Authorization : `Bearer ${token}`,
      };
    
    console.log(body)

    const request = axios.post('http://43.200.205.125:8000/board/', JSON.stringify(body), {headers})
    .then(res => {
      console.log(res.data);
      setImage({
        image_file: "",
        preview_URL: "img/default_image.jpg",
      })
      window.location.href='../board'
      //window.location.reload();
    }).catch(error => {
      console.log("error");
      console.log(error);

      
  });
  }


  const saveImage = (e) => {
    e.preventDefault();
    const fileReader = new FileReader();
    
    if(e.target.files[0]){
      fileReader.readAsDataURL(e.target.files[0])
      const formData = new FormData()
      formData.append('file', image.image_file);
      setImagefile(formData);
    }
    fileReader.onload = () => {
      setImage(
        {
          image_file: e.target.files[0],
          preview_URL: fileReader.result
        }
      )
    }
    
  }

  const deleteImage = () => {
    setImage({
      image_file: "",
      preview_URL: "img/default_image.jpg",
    });
  }
    let today = new Date(); // today 객체에 Date()의 결과를 넣어줬다
    let time = {
        year: today.getFullYear(),  //현재 년도
        month: today.getMonth() + 1, // 현재 월
        date: today.getDate(), // 현제 날짜
        hours: today.getHours(), //현재 시간
        minutes: today.getMinutes(), //현재 분
    };

    let published_date = `${time.year}/${time.month}/${time.date} ${time.hours}:${time.minutes}`;

    const body = {
        title: title,
        body: content,
        image: Image_file,
        published_date: published_date,
        category: category
      }

  return (
    <>
      <Wrapper>
        <div style={{marginTop: "7%" ,marginLeft: "20%",  flex:"1"}}>
          <div class="form__group field" style={{width:"80%"}}>
            <input type="input" class="form__field" placeholder="제목" name="name" id='name' required value={title} onChange={(event) => setTitle(event.target.value)}/>
          </div>
          <br/>
          <div class="form__group field" style={{width:"80%"}}>
            <input type="input" class="form__field" placeholder="내용" name="name" id='name' required value={content} onChange={(event) => setContent(event.target.value)}/>
          </div>
          <br/>
          <div class="form__group field" style={{width:"80%"}}>
            <input type="input" class="form__field" placeholder="카테고리" name="name" id='name' required value={category} onChange={(event) => setCategory(event.target.value)}/>
          </div>
          <div class="form__group field" style={{width:"80%"}}>
            <div style={{margin: "50px 0 20px 0", width: "200px", height: "200px"}} className="img-wrapper">
             <img style={{width: "200px", height: "200px"}} src={image.preview_URL} />
            </div>
            <br></br>
            <input 
            type="file" 
            accept="image/*" 
            name="file" 
            id='name' 
            onChange={saveImage}
            onClick={(e)=>e.target.value = null}
            ref={refParam => inputRef = refParam}
            style={{ display: "none" }}
            />
                  <div className="upload-button">
        <Button type="primary" variant="contained" onClick={() => inputRef.click()}>
          Preview
        </Button>
        <Button color="error" variant="contained" onClick={deleteImage}>
          Delete
        </Button>
      </div>
          </div>
          <br/>
          <div>
            <ButtonGroup>  
              <Button variant="outline-secondary" onClick={() => CreateBoard({body})}>작성하기</Button>
              <Button variant="outline-secondary" href='./board'>취소</Button>
            </ButtonGroup>
          </div>      
        </div>
      </Wrapper>
    </>
  )
}

export default Write;