import React from 'react';
import {
  CDBSidebar,
  CDBSidebarContent,
  CDBSidebarFooter,
  CDBSidebarHeader,
  CDBSidebarMenu,
  CDBSidebarMenuItem,
} from 'cdbreact';
import { NavLink } from 'react-router-dom';

const Sidebar = () => {
    return (
      <div style={{ display: 'flex', height: '100vh', overflow: 'scroll initial' }}>
        <CDBSidebar textColor="#fff" backgroundColor="#333">
          <CDBSidebarHeader prefix={<i className="fa fa-bars"></i>}>
            <a href="/" className="text-decoration-none" style={{ color: 'inherit' }}>
              Menu
            </a>
          </CDBSidebarHeader>
  
          <CDBSidebarFooter style={{ textAlign: 'center' }}>
          <div
              className="sidebar-btn-wrapper"
              style={{
                padding: '20px 5px',
              }}
            >
              <a href="/" className='text-decoration-none' style={{ color : 'inherit'}}>
                  홈
              </a>
            </div>
            <div
              className="sidebar-btn-wrapper"
              style={{
                padding: '20px 5px',
              }}
            >
              <a href="/board" className='text-decoration-none' style={{ color : 'inherit'}}>
                  게시판
              </a>
            </div>
            <div
              className="sidebar-btn-wrapper"
              style={{
                padding: '20px 5px',
              }}
            >
              <a href="/product" className='text-decoration-none' style={{ color : 'inherit'}}>
                  상품
              </a>
            </div>
            <div
              className="sidebar-btn-wrapper"
              style={{
                padding: '20px 5px',
              }}
            >
              <a href="/search" className='text-decoration-none' style={{ color : 'inherit'}}>
                  상품검색
              </a>
            </div>
            <div
              className="sidebar-btn-wrapper"
              style={{
                padding: '20px 5px',
              }}
            >
              <a href="/map" className='text-decoration-none' style={{ color : 'inherit'}}>
                  지도
              </a>
            </div>
          </CDBSidebarFooter>
        </CDBSidebar>
      </div>
    );
  };

export default Sidebar;