import React, { useEffect, useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { Form, Container } from 'react-bootstrap';
import styled from 'styled-components';
import axios from 'axios';

const Wrapper = styled.div`
    /* 레이아웃 */
    display: flex;
    flexDirection: row;
    width: 100%;
    max-width: 1100px;
    height: 100%;
    max-height: 550px;
    margin: 0 auto;
    align-items: center;
`;

const Tag = styled.div`
    /* 레이아웃 */
    flexDirection: row;
    width: 100%;
    max-width: 1100px;
    height: 100%;
    max-height: 550px;
    margin: 0 auto;
`;

const Items = styled.div`
    /* 레이아웃 */
    display: flex;
    flexDirection: row;
    width: 100%;
    max-width: 1100px;
    height: 100%;
    max-height: 550px;
    margin: 0 auto;
    justify-content: space-between;

`;

const Temp = styled.p`
    font-size: small;
    text-align: center;
`


const ProductModal = ({ show, onHide, name, pk, src, pname, event, price, category, views, likes }) => {

    const [posts, setPosts] = useState([]);

    let productlist = [
    ]    

    const onLikeHandler = (pk) => {
        const token = localStorage.getItem('token');
        const headers = {
            "Content-Type" : "application/json",
            Authorization : `Bearer ${token}`,
          };

        axios.get(`http://43.200.205.125:8000/product/like_product/${pk}/`, {headers})
        .then(res => {
            console.log(res);

          })
    }

    useEffect(() => {
        productlist = [];
      let url = `http://43.200.205.125:8000/product?category=Icecream`
          axios.get(url)
          .then(res => setPosts(res.data))
          .catch(error => console.log(error))

          return () => {
            console.log('컴포넌트가 화면에서 사라짐');
          };
  }, [category]);

    if(posts.length == 0) {
          
    for (let i = 0; i < 0; i++) {
      const obj = posts[i];
      const keys = Object.keys(obj);

      const Objproduct = {
          pk : obj[keys[0]],
          pname: obj[keys[1]],
          category: obj[keys[2]],
          price: obj[keys[4]],
          image: obj[keys[6]],
          // pevent: "",
        }
        productlist.push(Objproduct);
    }
  } else {
      let rand = Math.floor(Math.random() * (30 - 0 + 1)) + 0;
    for (let i = rand; i < rand + 4; i++) {
      const obj = posts[i];
      const keys = Object.keys(obj);

      const Objproduct = {
          pk : obj[keys[0]],
          pname: obj[keys[1]],
          category: obj[keys[2]],
          price: obj[keys[4]],
          image: obj[keys[6]],
          // pevent: "",
        }
        productlist.push(Objproduct);
    }

}

return (
        <Container>
        <Modal
            show={show}
            onHide={onHide}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
                {name}
            </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Wrapper>
                    <img style={{}} src={src}/>
                    <Tag>
                        <h2>{pname}</h2>
                        <br/>
                        {/* <h6>식품 첨가물을 넣지 않은 끌레도르 딸기 아이스크림</h6> */}
                        <br/>
                        <h2>{price}원</h2>
                        <br/>
                        <b style={{backgroundColor: "pink"}}className="product_des">{event}</b>
                        <span onClick={() => onLikeHandler(pk)}>
                            👍{likes.length}
                        </span>
                        👁{views.length}
                    </Tag>
                    
                </Wrapper>
                <Items>
                    {productlist.map(({ image, pname, pevent, price, category}) => (
                        <div style={{height: "20%", width: "20%"}}>
                            <img style={{height: "100px", width: "100%"}} src={image}/>
                            <Temp>{pname}</Temp>
                            <Temp>{price}원 <b style={{backgroundColor: "pink"}}className="product_des">{pevent}</b></Temp>
                        </div>
                        
                    ))
                    }
                </Items>
            </Modal.Body>
            <Modal.Footer>
            <Button onClick={onHide}>Close</Button>
            </Modal.Footer>
        </Modal>
        </Container>
    );
}


export default ProductModal