import React from "react";
import './Product.css'
import styled from 'styled-components';
import Paging from "./Paging";
import { useState } from "react";
import ProductItem from "./Productitem";
import { Button, Form } from "react-bootstrap";
import Pagination from "./Pagination";
import { useEffect } from "react";
import axios from "axios";
import Productlist from "./productlist";

const Wrapper = styled.div`
    /* 레이아웃 */
    display: flex;
    flexDirection: row;
    position: fixed;
    width: 100%;
    height: 100%;
    max-height: 550px;
    margin: 0 auto;
    margin-top: 50px;
    align-items: center;
    justify-content: space-between;
    overflow-y : auto;
`;

const Cnt = styled.form`
    display: flex;
    align-items: center;
    position: inherit;
    width: 100%;
    justify-content: center;
    //background-color: black;
    top: 10%;
`;

const Sort = styled.div`
    display: flex;
    align-items: center;
    position: inherit;
    width: 100%;
    justify-content: center;
    //background-color: black;
    top: 18%;
`;

const Buttons = styled.div`
  display: flex;
  width: 70%;

  justify-content: space-between;
`

function Product(props) {

    const [posts, setPosts] = useState([]);
    const [postsp, setPostsp] = useState([]);
    const [limit, setLimit] = useState(16);
    const [page, setPage] = useState(1);
    const offset = (page - 1) * limit;
    const [search, setSearch] = useState('');

    const [choice, setChoice] = useState(0);
    // const productlist = props.all;
    // const productlistp = props.price_asc;
    // const productlistpr = props.price_desc;
    // const productlistmeal = props.meal;
    // const productlistice = props.ice;
    // const productlistfood = props.food;
    // const productlistnec = props.nec;
    // const productlistsnack = props.snack;
    // const productlistbever = props.beverage;
    // const productlistdiary = props.diary;

    const productlist = JSON.parse(Object.assign(localStorage.getItem('all'))) 
    const productlistp = JSON.parse(Object.assign(localStorage.getItem('price_asc')))
    const productlistpr = JSON.parse(Object.assign(localStorage.getItem('price_desc')))
    const productlistmeal = JSON.parse(Object.assign(localStorage.getItem('meal')))
    const productlistice = JSON.parse(Object.assign(localStorage.getItem('ice')))
    const productlistfood = JSON.parse(Object.assign(localStorage.getItem('food')))
    const productlistbever = JSON.parse(Object.assign(localStorage.getItem('beverage')))
    const productlistnec = JSON.parse(Object.assign(localStorage.getItem('nec')))
    const productlistdiary = JSON.parse(Object.assign(localStorage.getItem('diary')))
    const productlistsnack = JSON.parse(Object.assign(localStorage.getItem('snack')))


    const onChoiceHandler = (event) => {
      setChoice(event);
      setPage(1);
    };
    
 
    if(choice == 1) {
      return (
        <Productlist plist={productlistpr} onChoiceHandler={onChoiceHandler}/>
      );
    } else if(choice == 0) {
      return (
        <Productlist plist={productlistp} onChoiceHandler={onChoiceHandler}/>
      );
    } else if(choice == 2) {
      return (
        <Productlist plist={productlistmeal} onChoiceHandler={onChoiceHandler}/>
      );
    } else if(choice == 3) {
      return (
        <Productlist plist={productlistsnack} onChoiceHandler={onChoiceHandler}/>
      );
    } else if(choice == 4) {
      return (
        <Productlist plist={productlistbever} onChoiceHandler={onChoiceHandler}/>
      );
    } else if(choice == 5) {
      return (
        <Productlist plist={productlistice} onChoiceHandler={onChoiceHandler}/>
      );
    } else if(choice == 6) {
      return (
        <Productlist plist={productlistfood} onChoiceHandler={onChoiceHandler}/>
      );
    } else if(choice == 7) {
      return (
        <Productlist plist={productlistnec} onChoiceHandler={onChoiceHandler}/>
      );
    } else if(choice == 9) {
      return (
        <Productlist plist={productlist} onChoiceHandler={onChoiceHandler}/>
      );
    } else {
      return (
        <Productlist plist={productlistdiary} onChoiceHandler={onChoiceHandler}/>
      );
    }
    
}

export default Product;