import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { Form, Container } from 'react-bootstrap';
import axios from 'axios';




const SignUp = ({ show, onHide }) => {

    const [Email, setEmail] = useState("");
    const [Name, setName] = useState("");
    const [Password, setPassword] = useState("");
    const [ConfirmPassword, setConfirmPassword] = useState("");
    const [Gender, setGender] = useState("M");
    const [form, setForm] = useState({
      year: "2020",
      month: "01",
      day: "01",
    });

    const onEmailHandler = (event) => {
        setEmail(event.currentTarget.value);
    }
    const onNameHandler = (event) => {
        setName(event.currentTarget.value);
    }
    const onPasswordHandler = (event) => {
        setPassword(event.currentTarget.value);
    }
    const onConfirmPasswordHandler = (event) => {
        setConfirmPassword(event.currentTarget.value);
    }
    const onGenderHandler = (event) => {
        setGender(event.currentTarget.value);
      }

    
    const onSubmitHandler = (event) => {
        event.preventDefault();

        if(Password !== ConfirmPassword){
            return alert('비밀번호와 비밀번호 확인이 같지 않습니다.')
        }
        
        const headers = {
          "Content-Type" : "application/json",
          // Authorization : `Bearer`,
        };
        
        let Birth = form.year + "-" + form.month + "-" + form.day;
        console.log(Birth);
        let body = {
            username: Name,
            email: Email,
            password: Password,
            password2: ConfirmPassword,
            birth: Birth,
            gender: Gender,
          }

          let Signupcheck = false;

          console.log(headers)
          console.log(JSON.stringify(body))
          console.log(body)
          const request = axios.post('http://43.200.205.125:8000/auth/register/', JSON.stringify(body), {headers})
          .then(res => {
            console.log("?");
            console.log(res);
            Signupcheck = true;
            window.location.reload();
          })

          if(!Signupcheck) {
            console.log(Signupcheck)
            Signupcheck = true;
            setName('');
            setEmail('');
            setPassword('');
            setConfirmPassword('');
            setForm({ ...form, year: "2000", month: "01", day: "01" })
            setGender('');
            alert("회원가입 실패");
          }

    }
    const onCheckEnter = (e) => {
      if(e.code === 'Enter') {
        onSubmitHandler(e);
      }
    }
    const SelectBox = () => {
      return (
        <select value={Gender} onChange={onGenderHandler}>
          <option key="male" value="M">Male</option>
          <option key="female" value="F">Female</option>
        </select>
      );
    };

    // date

    const now = new Date();
  
    let years = [];
    for (let y = now.getFullYear(); y >= 1930; y -= 1) {
      years.push(y);
    }
  
    let month = [];
    for (let m = 1; m <= 12; m += 1) {
      if (m < 10) {
        // 날짜가 2자리로 나타나야 했기 때문에 1자리 월에 0을 붙혀준다
        month.push("0" + m.toString());
      } else {
        month.push(m.toString());
      }
    }
   let days = [];
    let date = new Date(form.year, form.month, 0).getDate();
    for (let d = 1; d <= date; d += 1) {
      if (d < 10) {
       // 날짜가 2자리로 나타나야 했기 때문에 1자리 일에 0을 붙혀준다
        days.push("0" + d.toString());
      } else {
        days.push(d.toString());
      }
    }

  return (
        <Container>
        <Modal
          show={show}
          onHide={onHide}
          size="lg"
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
              Sign-Up
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>

            <Form onKeyPress={onCheckEnter}>
              <Form.Group className="mb-3">
                    <Form.Label>User Name</Form.Label>
                    <Form.Control type="text" value={Name} onChange={onNameHandler} placeholder="User Name" />
                </Form.Group>

                <Form.Group className="mb-3">
                    <Form.Label>E-mail</Form.Label>
                    <Form.Control type="text" value={Email} onChange={onEmailHandler} placeholder="E-mail" />
                </Form.Group>

                <Form.Group className="mb-3">
                    <Form.Label>Password</Form.Label>
                    <Form.Control type="password" value={Password} onChange={onPasswordHandler} placeholder="Password" />
                </Form.Group>

                <Form.Group className="mb-3">
                    <Form.Label>Password Check</Form.Label>
                    <Form.Control type="password" value={ConfirmPassword} onChange={onConfirmPasswordHandler} placeholder="Password Check" />
                </Form.Group>

                <Form.Group className="mb-3">
                    <Form.Label>Birth</Form.Label>
                    <br/>
                    <select
                      value={form.year}
                      onChange={(e) =>
                        setForm({ ...form, year: e.target.value })
                      }
                    >
                      {years.map(item => (
                        <option value={item} key={item}>
                            {item}
                          </option>
                      ))}
                    </select>
                    <select
                      value={form.month}
                      onChange={(e) =>
                        setForm({ ...form, month: e.target.value })
                      }
                    >
                      {month.map(item => (
                        <option value={item} key={item}>
                            {item}
                          </option>
                      ))}
                    </select>
                    <select
                      value={form.day}
                      onChange={(e) =>
                        setForm({ ...form, day: e.target.value })
                      }
                    >
                      {days.map(item => (
                        <option value={item} key={item}>
                            {item}
                          </option>
                      ))}
                    </select>
                </Form.Group>

                <Form.Group className="mb-3">
                    <Form.Label>Gender</Form.Label>
                    <br/>
                    <SelectBox/>
                </Form.Group>
            </Form>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="primary" onClick={onSubmitHandler} type="submit">
                Submit
            </Button>
            <Button onClick={onHide}>Close</Button>
          </Modal.Footer>
        </Modal>
        </Container>
    );
}

export default SignUp