import React from 'react';
import CommonTable from './CommonTable';
import CommonTableColumn from './CommonTableColumn';
import CommonTableRow from './CommonTableRow';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { Button } from 'react-bootstrap';
import { useState } from 'react';
import { useEffect } from 'react';
import axios from 'axios';
import Pagination from "./Pagination";
import Paging from './Paging';
import "./Paging.css"

const getRequestParams = () => {

  const API_KEY = process.env.REACT_APP_API_KEY

  const url = `http://127.0.0.1:8000/boards/?key=${API_KEY}`;

  return {
      url,
  };
}

function Board() {
  const [posts, setPosts] = useState([]);
  const [limit, setLimit] = useState(5);
  const [page, setPage] = useState(1);
  const offset = (page - 1) * limit;

  const boardlist = [
]


  useEffect(() => {
    axios.get("http://43.200.205.125:8000/board/")
        .then(res => setPosts(res.data))
        .catch(error => console.log(error))
}, []);

  console.log(boardlist);
  console.log('Hi');
  console.log(posts);
  // console.log(typeof(posts))
  // const keys = Object.keys(posts)
  // console.log(keys);
  // console.log(posts.length);
  
  for (let i = 0; i < posts.length; i++) {
    const obj = posts[i];
    const keys = Object.keys(obj);
    // pk
    console.log(obj[keys[0]]);

    // author
    console.log(obj[keys[1]]);
    const objAuthor = obj[keys[1]];
    const keysAuthor = Object.keys(objAuthor);
    // auth pk
    console.log(objAuthor[keysAuthor[0]]);
    // auth username
    console.log(objAuthor[keysAuthor[1]]);
    // auth email
    console.log(objAuthor[keysAuthor[2]]);
    // auth created_date
    console.log(objAuthor[keysAuthor[3]]);

    // title
    console.log(obj[keys[2]]);
    // category
    console.log(obj[keys[3]]);
    // body
    console.log(obj[keys[4]]);
    // image
    console.log(obj[keys[5]]);
    // published_date
    console.log(obj[keys[6]].split('T')[0]);

    const Objboard = {
      id : obj[keys[0]],
      category : obj[keys[3]],
      title : obj[keys[2]],
      createDate : obj[keys[6]].split('T')[0],
      content : obj[keys[4]]
    }

    boardlist.push(Objboard);
  }
  console.log("hye");
  

  return (
    <Wrapper>
    <div style={{marginTop: "7%"}}>
    
    <CommonTable headersName={['글번호', '카테고리', '제목', '등록일'/*, '상품명'*/]}>
        {boardlist.slice(offset, offset + limit).map(({ id, category, title, createDate,content }) => (
          <CommonTableRow>
            <CommonTableColumn>{id}</CommonTableColumn>
            <CommonTableColumn>{category}</CommonTableColumn>
            <Link to={`/detail/${id}`} className="text-decoration-none" style={{ color: 'inherit' }}>
              <CommonTableColumn>{title}</CommonTableColumn>
            </Link>
            <CommonTableColumn>{createDate}</CommonTableColumn>
            {/* <CommonTableColumn>{content}</CommonTableColumn> */}
          </CommonTableRow>
        ))}
        <br/>
        <div style={{marginLeft:"0%", marginTop:"13%", position: "fixed"}}>
          <Button variant="outline-secondary" href='./write'>작성하기</Button>
          
        </div>

      <div style={{marginLeft:"30%", marginTop:"13%", position: "fixed"}}>
            <Paging
              total={boardlist.length}
              limit={limit}
              page={page}
              setPage={setPage}
              />
      </div>  
      </CommonTable>

    </div>
    </Wrapper>
  );
}

const Wrapper = styled.div`
    /* 레이아웃 */
    display: flex;
    flex-direction: column;
    position: fixed;
    width: 100%;
    // max-width: 1100px;
    // height: 100%;
    overflow-y : auto;
`;

export default Board;
