import React, { useState } from "react";
import Slider from "react-slick";
import "./index.css";
import ProductModal from "./Product/Productmodal";


const Slide = (props) => {
  const [setproduct, setProductOn] = useState(false);

  var settings = {
      dots: true,
      autoplay: true,
      autoplaySpeed: 5000,
      slidesToShow: 5,
    };
    return (
      <div className="container">
        {/* <ProductModal src={props.src} pname={props.pname} event={props.event} price={props.price} show={setproduct} onHide={() => setProductOn(false)}/> */}
        <Slider {...settings}>
          <div>
              <img onClick={() => setProductOn(true)} src="https://cdn.imweb.me/thumbnail/20220912/f237a0c1030f1.jpg" width="200px" height="200px"/>
          </div>
          <div>
              <img onClick={() => setProductOn(true)} src="https://cdn.imweb.me/thumbnail/20220912/a99e5cc089280.jpg" width="200px" height="200px"/>
          </div>
          <div>
              <img onClick={() => setProductOn(true)} src="https://cdn.imweb.me/thumbnail/20220912/a7db8bb18436d.jpg" width="200px" height="200px"/>
          </div>
          <div>
              <img onClick={() => setProductOn(true)} src="https://cdn.imweb.me/thumbnail/20220912/84cdce1163764.jpg" width="200px" height="200px"/>
          </div>
          <div>
              <img onClick={() => setProductOn(true)} src="https://cdn.imweb.me/thumbnail/20220912/0ef180121709c.jpg" width="200px" height="200px"/>
          </div>
          <div>
              <img onClick={() => setProductOn(true)} src="https://cdn.imweb.me/thumbnail/20220912/162e28db5d263.jpg" width="200px" height="200px"/>
          </div>
        </Slider>
      </div>
    );
};

export default Slide;