import axios from "axios";
import React, { useState } from "react";
import { useEffect } from "react";
import { Button, ButtonGroup } from "react-bootstrap";
import styled from 'styled-components';
import ChangePW from "./ChangePW";
import Delete from "./Delete";


const Wrapper = styled.div`
    /* 레이아웃 */
    display: flex;
    
    position: fixed;
    width: 96%;
    height: 70%;
    max-height: 100%;
    max-width: 50%;
    margin: 0 auto;
    margin-top: 100px;
    margin-left: 280px;
    overflow-y : auto;
`;

const Content = styled.footer`
    display; flex;

    width: 100%;
    height: 70%;
    text-align:left;
    margin-top: auto;

    align-items: center;
`;

const Title = styled.header`
    display; flex;
    position: fixed;
    width: 50%;
    height: 15%;
    text-align:center;
    align-items: center;
`;

const Profile_data = styled.div`
    display: flex;
    flex-direction: row;  
`
const Button_place = styled.div`
    position: fixed;
    margin-left: 14%;
    top: 75%;
`


const MYPAGE = () => {
    const [changepwOn, setchangepwOn] = useState(false);
    const [deleteOn, setdeleteOn] = useState(false);
    const [posts, setPosts] = useState([]);
    let profile;
    useEffect(() => {
        const token = localStorage.getItem("token");
        const headers = {
            "Content-Type" : "application/json",
            Authorization : `Bearer ${token}`,
          };
        const getPosts = async () => {
          await axios.get("http://43.200.205.125:8000/auth/profile", {headers})
          .then(res => setPosts(res.data))
          .catch(error => console.log(error));
          
            console.log(posts);

        };
          getPosts();

      }, []);
      console.log(posts);
      const obj = posts;
      const keys = Object.keys(obj);
      profile = {
        "pk": obj[keys[0]],
        "username": obj[keys[1]],
        "email": obj[keys[2]],
        "created_at": obj[keys[3]],
        "gender": obj[keys[4]],
        "birth": obj[keys[5]],
        "image": `http://43.200.205.125:8000/${obj[keys[6]]}`
      };
      console.log(keys)
    //   {
    //     "pk": 3,
    //     "username": "test12",
    //     "email": "test12@naver.com",
    //     "created_at": "2022-11-12T17:27:35.111979+09:00",
    //     "gender": "Male",
    //     "birth": "1999-03-03",
    //     "image": "/media/default_user.png"
    //   }


    return (
            <Wrapper>
                <Title><h3>마이페이지</h3></Title>

                <Content>
                    <Profile_data>
                        <div style={{width: "50%"}}>
                            <img style={{height: "70%", width: "70%"}} src={profile.image}/>
                        </div>
                        <div style={{}}>
                            <p>이름 <b>{profile.username}</b></p>
                            <p>E-mail <b>{profile.email}</b></p>
                            <p>생년월일 <b>{profile.birth}</b></p>
                            <p>성별 <b>{profile.gender}</b></p>
                        </div>
                    </Profile_data>
                    

                </Content>
                <Button_place>
                <ButtonGroup>
                    <Button variant="outline-secondary" onClick={() => setchangepwOn(true)} type="submit">
                        Change Password
                    </Button>
                    <Button variant="outline-secondary" onClick={() => setdeleteOn(true)} type="submit">
                        Delete Account
                    </Button>
                    </ButtonGroup>
                    <ChangePW show={changepwOn} onHide={() => setchangepwOn(false)}/>
                    <Delete show={deleteOn} onHide={() => setdeleteOn(false)}/>
                </Button_place>
            </Wrapper>
    );
}

export default MYPAGE;