import React, { useState } from 'react';
import styled from 'styled-components';
import oc from 'open-color';
import SignIn from './SignIn';
import SignUp from './SignUp';

const Wrapper = styled.div`
    /* 레이아웃 */
    display: flex;
    position: fixed;
    align-items: center;
    justify-content: space-between;
    height: 60px;
    width: 95%;
    top: 0px;
    z-index: 5;

    /* 색상 */
    background: ${oc.white[0]};
    color: black;
    border-bottom: 2px solid ${oc.black[5]};
    // box-shadow: 0 3px 6px rgba(0,0,0,0.10), 0 3px 6px rgba(0,0,0,0.20);


    /* 폰트 */
    font-size: 1.5rem;

`;

const Name = styled.p`
    display:flex;
    font-size:1.5rem;
    align-items: center;
    justify-content: center;
    width:40%;
`;

const Gnb = styled.ul`
    // 모바일 
    display:flex;
    flex-direction:row;
    list-style:none;
    // align-items: center;
    right: 5%;
    & > li{
        margin-left:40px;
    } 
    // pc 내용들 
    @media only screen and (max-width:768px){
        display:none;
    }

    font-size:1rem;
`;
    
function LoginButton() {
    const [signinOn, setsigninOn] = useState(false);
    const [signupOn, setsignupOn] = useState(false);
    return (
        <Gnb>
            <SignIn show={signinOn} onHide={() => setsigninOn(false)}/>
            <SignUp show={signupOn} onHide={() => setsignupOn(false)}/>
            <li>
                <a onClick={() => setsigninOn(true)} className='text-decoration-none' style={{ color : 'inherit'}}>
                    LOGIN
                </a>
            </li>
            <li>
                <a onClick={() => setsignupOn(true)} className='text-decoration-none' style={{ color : 'inherit'}}>
                    SIGNUP
                </a>
            </li>
        </Gnb>
    );
}
function LogoutButton() {

    const LogoutEvent = (event) => {
        localStorage.removeItem("token");
        window.location.reload();
    } 

    return (
        <Gnb>
            <li>
                <a onClick={LogoutEvent} className='text-decoration-none' style={{ color : 'inherit'}}>
                    LOGOUT
                </a>
            </li>
            <li>
                <a href='/mypage' className='text-decoration-none' style={{ color : 'inherit'}}>
                    MYPage
                </a>

            </li>
        </Gnb>
    )
}

const Header = () => {
    const isLogin = false;
    const token = localStorage.getItem("token")

    console.log(token)
    if(token) {
        return (
            <Wrapper>
                <p></p>
                <p></p>
                <Name>편의점추천홈페이지</Name>
                <LogoutButton/>
            </Wrapper>
        );
    } else {
        return (
            <Wrapper>
                <p></p>
                <p></p>
                <Name>편의점추천홈페이지</Name>
                <LoginButton/>
            </Wrapper>
        );
    }
  
}

export default Header;