import axios from "axios";
import { useEffect, useState } from "react";
import { CustomOverlayMap, Map, MapMarker } from "react-kakao-maps-sdk";
import styled from 'styled-components';
import {useMap} from 'react-map-gl';
import "./mapevent.css";

const Wrapper = styled.div`
    /* 레이아웃 */
    display: flex;
    position: fixed;
    width: 96%;
    max-width: 1100px;
    height: 100%;
    margin: 0 auto;
    align-items: center;
    justify-content: space-between;
    margin-left : 8%;
    
`;

const KAKAOMAP = () => {
    const [posts, setPosts] = useState([]);
    const [isOpen, setIsOpen] = useState(false)

    const CUpositions = [

    ]
    const GSpositions = [

    ]

      const [state, setState] = useState({
        center: {
          lat: 37.5463,
          lng: 126.9265,
        },
        errMsg: null,
        isLoading: true,
      })
    
      useEffect(() => {
        if (navigator.geolocation) {
          // GeoLocation을 이용해서 접속 위치를 얻어옵니다
          navigator.geolocation.getCurrentPosition(
            (position) => {
              setState((prev) => ({
                ...prev,
                center: {
                  lat: position.coords.latitude, // 위도
                  lng: position.coords.longitude, // 경도
                },
                isLoading: false,
              }))
            },
            (err) => {
              setState((prev) => ({
                ...prev,
                errMsg: err.message,
                isLoading: false,
              }))
            }
          )
        } else {
          // HTML5의 GeoLocation을 사용할 수 없을때 마커 표시 위치와 인포윈도우 내용을 설정합니다
          setState((prev) => ({
            ...prev,
            errMsg: "geolocation을 사용할수 없어요..",
            isLoading: false,
          }))
        }
      }, [])

      useEffect(() => {
        // 임시
        let url = `http://43.200.205.125:8000/product/store/distance/?longitude=${state.center.lng}&latitude=+${state.center.lat}`;
        axios.get(url)
            .then(res => setPosts(res.data))
            .catch(error => console.log(error))
    }, []);

    console.log(posts.length);
    for (let i = 0; i < posts.length; i++) {
        const obj = posts[i];
        const keys = Object.keys(obj);
       
        if(obj[keys[3]].includes('CU')) {
          const Objlocation = {
            business_name : obj[keys[3]],
            title : obj[keys[4]],
            latlng: { lat: obj[keys[8]], lng: obj[keys[7]] },
          }
          CUpositions.push(Objlocation);
        } else {
          const Objlocation = {
            business_name : obj[keys[3]],
            title : obj[keys[4]],
            latlng: { lat: obj[keys[8]], lng: obj[keys[7]] },
          }
          GSpositions.push(Objlocation);
        }
          
      }
//      console.log(positions.length);
    // console.log(state.center);
  let center_lat = state.center.lat;
  let center_lng = state.center.lng;
  console.log(center_lat);
  console.log(center_lng);
  let temp = {
    latlng: { lat: 37.55011, lng: 126.92460 },
  }

  // console(localStorage.getItem('all'));

  const EventMarkerContainer = ({ position, image, content, title, business_name, brand_image, brand }) => {
    const [isVisible, setIsVisible] = useState(false)
    const [isOpen, setIsOpen] = useState(false)

    return (
      <MapMarker
        position={position} // 마커를 표시할 위치
        // @ts-ignore
        // onClick={(marker) => map.panTo(marker.getPosition())}
        onMouseOver={
          // 마커에 마우스오버 이벤트가 발생하면 인포윈도우를 마커위에 표시합니다
          () => setIsOpen(true)
        }
        // 마커에 마우스아웃 이벤트를 등록합니다
        onMouseOut={
          // 마커에 마우스아웃 이벤트가 발생하면 인포윈도우를 제거합니다
          () => setIsOpen(false)
        }


        image = {image}
      >
        {isOpen && (<CustomOverlayMap position={position}>
            <div className="wrap">
              <div className="info">
                <div className="title">
                  {title}
                  <div
                    className="close"
                    onClick={() => setIsOpen(false)}
                    title="닫기"
                  ></div>
                </div>
                <div className="body">
                  <div className="img">
                    <img
                      src={brand_image}
                      width="73"
                      height="70"
                      alt="logo"
                    />
                  </div>
                  <div className="desc">
                    <div className="ellipsis">
                      <b>{brand}{title}</b>
                    </div>
                    <div className="ellipsis">
                      {business_name}
                    </div>
                  </div>
                </div>
              </div>
            </div>
            ;
          </CustomOverlayMap>)}
      </MapMarker>
    )
  }




  return (
    <Wrapper>
        <Map
        center={{ /*lat: center_lat, lng: center_lng*/ lat: 37.55011, lng: 126.92460 }}
        style={{width: "100%", height: "400px" }}
        >
        <MapMarker
        position={temp.latlng} // 마커를 표시할 위치
        image={{
          src: "img/center.png", // 마커이미지의 주소입니다
            size: {
            width: 25,
            height: 30
          }, // 마커이미지의 크기입니다
        }}/> 
        

        {CUpositions.map((value) => (
        <EventMarkerContainer
          key={`EventMarkerContainer-${value.latlng.lat}-${value.latlng.lng}`}
          position={value.latlng}
          // content={value.content}
          title={value.title}
          business_name={value.business_name}
          brand_image={"img/CU_.png"}
          brand={"CU"}
          image={{
            src: "img/cu.png", // 마커이미지의 주소입니다
            size: {
              width: 25,
              height: 30
            }, // 마커이미지의 크기입니다
          }}
        />
        ))}

        {GSpositions.map((value) => (
        <EventMarkerContainer
          key={`EventMarkerContainer-${value.latlng.lat}-${value.latlng.lng}`}
          position={value.latlng}
          // content={value.content}
          title={value.title}
          business_name={value.business_name}
          brand_image={"img/gs25.png"}
          brand={"GS25"}
          image={{
            src: "img/gs.png", // 마커이미지의 주소입니다
            size: {
              width: 25,
              height: 30
            }, // 마커이미지의 크기입니다
          }}
        />
        ))}

        </Map>
    </Wrapper>
  );
}

export default KAKAOMAP;