function Productlist({plist, onChoiceHandler}) {

   const [limit, setLimit] = useState(16);
    const [page, setPage] = useState(1);
    const offset = (page - 1) * limit;
    const [view, setView] = useState(false); 
    

 
    return (
        <Wrapper>

          <Sort style={{backgroundColor: "white"}}>
{/* 
    asc : 0
    desc : 1
    Meal :2
    Snacl : 3
    Beverage : 4
    Ice : 5
    Food : 6
    Necessaries : 7
    Dairy : 8
*/}
              <ButtonGroup>   
              <Button variant="outline-secondary" size="sm" onClick={()=>{onChoiceHandler(2)}}>
                Meal
              </Button>
              <Button variant="outline-secondary" size="sm" onClick={()=>{onChoiceHandler(3)}}>
                Snack
              </Button>
              <Button variant="outline-secondary" size="sm" onClick={()=>{onChoiceHandler(4)}}>
                Beverage
              </Button>
              <Button variant="outline-secondary" size="sm" onClick={()=>{onChoiceHandler(5)}}>
                Ice-Cream
              </Button>
              <Button variant="outline-secondary" size="sm" onClick={()=>{onChoiceHandler(6)}}>
                Food
              </Button>
              <Button variant="outline-secondary" size="sm" onClick={()=>{onChoiceHandler(7)}}>
                Necessaries
              </Button>
              <Button variant="outline-secondary" size="sm" onClick={()=>{onChoiceHandler(8)}}>
                DairyProduct
              </Button>
              </ButtonGroup>
            <DropdownButton variant="outline-secondary" id="dropdown-basic-button" title="">
                <Dropdown.Item onClick={()=>{onChoiceHandler(9)}}>전체</Dropdown.Item>
                <Dropdown.Item value={0} onClick={()=>{onChoiceHandler(0)}}>가격낮은순</Dropdown.Item>
                <Dropdown.Item value={1} onClick={()=>{onChoiceHandler(1)}}>가격높은순</Dropdown.Item>
            </DropdownButton>         
         </Sort>
          
        <div className="product_container">
              
                {plist.slice(offset, offset + limit).map(({pk, image, pname, pevent, price, category, views, likes}) => (
                    <ProductItem pk = {pk} src = {image} pname = {pname} event = {pevent} price={price} category={category} views={views} likes={likes}></ProductItem>
                ))}
        </div>
  
        <div className="page_">
            <Paging
              total={plist.length}
              limit={limit}
              page={page}
              setPage={setPage}
            />
        </div>  
      </Wrapper>
);
}

export default Productlist;