import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { Form, Container } from 'react-bootstrap';
import axios from 'axios';

const Delete = ({ show, onHide }) => {


  const [Password, setPassword] = useState("");

  const onPasswordHandler = (event) => {
      setPassword(event.currentTarget.value);
  }

const onSubmitHandler = (event) => {
      event.preventDefault();
      
      const headers = {
        "Content-Type" : "application/json",
        // Authorization : `Bearer`,
      };
      
      let body = {
          password: Password,
        }

        // console.log(headers)
        // console.log(JSON.stringify(body))
        // console.log(body)
        const request = axios.post('http://43.200.205.125:8000/auth//', JSON.stringify(body), {headers})
        .then(res => {
          // console.log(res.data);
          localStorage.setItem("token", res.data.access);
          const token = localStorage.getItem("token");
          // console.log(token)
          window.location.reload();
        })
        const token = localStorage.getItem("token");
        if(!token) {
          setPassword('');
          alert('로그인 실패');
        }
  }
  const onCheckEnter = (e) => {
    if(e.code === 'Enter') {
      onSubmitHandler(e);
    }
  }


  return (
        <Container>
        <Modal
          show={show}
          onHide={onHide}
          size="lg"
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
              Delete Account
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>

            <Form onKeyPress={onCheckEnter}>
                <Form.Group className="mb-3">
                    <Form.Label>Password</Form.Label>
                    <Form.Control type="password" value={Password} onChange={onPasswordHandler} placeholder="Enter Password" />
                </Form.Group>
            </Form>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="primary" onClick={onSubmitHandler} type="submit">
                Submit
            </Button>
            <Button onClick={onHide}>Close</Button>
          </Modal.Footer>
        </Modal>
        </Container>
    );
}

export default Delete