import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { Form, Container } from 'react-bootstrap';
import axios from 'axios';

const SignIn = ({ show, onHide }) => {

  let islogin = false;
  const [Email, setEmail] = useState("");
  const [Password, setPassword] = useState("");

  const onEmailHandler = (event) => {
      setEmail(event.currentTarget.value);
  }
  const onPasswordHandler = (event) => {
      setPassword(event.currentTarget.value);
  }

const onSubmitHandler = (event) => {
      event.preventDefault();
      
      const headers = {
        "Content-Type" : "application/json",
        // Authorization : `Bearer`,
      };
      
      let body = {
          email: Email,
          password: Password,
        }

        // console.log(headers)
        // console.log(JSON.stringify(body))
        // console.log(body)
        const request = axios.post('http://43.200.205.125:8000/auth/login/', JSON.stringify(body), {headers})
        .then(res => {
          console.log(res.data);
          localStorage.setItem("token", res.data.access);
          islogin = false;
          console.log(islogin);
          const token = localStorage.getItem("token");
          // API 요청하는 콜마다 헤더에 accessToken 담아 보내도록 설정
          axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
          console.log("?");
          window.location.reload();
        })
        const token = localStorage.getItem("token");
        
        // if(!islogin && !token) {
        //   islogin = true;
        //   setEmail('');
        //   setPassword('');
        //   alert('로그인 실패');
        // }
  }



  const onCheckEnter = (e) => {
    if(e.code === 'Enter') {
      onSubmitHandler(e);
    }
  }


  return (
        <Container>
        <Modal
          show={show}
          onHide={onHide}
          size="lg"
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
              Sign-In
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>

            <Form onKeyPress={onCheckEnter}>
                <Form.Group className="mb-3">
                    <Form.Label>Email</Form.Label>
                    <Form.Control type="text" value={Email} onChange={onEmailHandler} placeholder="Enter ID" />
                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicPassword">
                    <Form.Label>Password</Form.Label>
                    <Form.Control type="password"  value={Password} onChange={onPasswordHandler} placeholder="Password" />
                </Form.Group>
            </Form>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="primary" onClick={onSubmitHandler} type="submit">
                Submit
            </Button>
            <Button onClick={onHide}>Close</Button>
          </Modal.Footer>
        </Modal>
        </Container>
    );
}

export default SignIn