import React, { useRef } from "react";
import { Button, ButtonGroup } from "react-bootstrap";
import { Link, useLocation, useParams } from "react-router-dom";
import styled from 'styled-components';
import './VocView.css';
import axios from "axios";
import { useEffect, useState } from "react";


const Wrapper = styled.div`
    /* 레이아웃 */
    display: flex;
    flexDirection: row;
    position: fixed;
    width: 100%;
    //max-width: 1100px;
     max-height:100%;
    // margin: 0 auto;
    // align-items: center;
    //margin-left: 280px;
    overflow-y : auto;
`;


const getRequestParams = (id) => {

    const API_KEY = process.env.REACT_APP_API_KEY
  
    const url = `http://127.0.0.1:8000/boards/${id}/?key=${API_KEY}`;
  
    return {
        url,
    };
  }

function BoardItem() {

    let { id } = useParams();
    const [posts, setPosts] = useState([]);
    const [com, setComs] = useState("");
    const [comments, setComments] = useState([]);


    const onLikeHandler = () => {
        const token = localStorage.getItem('token');
        const headers = {
            "Content-Type" : "application/json",
            Authorization : `Bearer ${token}`,
          };

        axios.get(`http://43.200.205.125:8000/board/like_board/${id}/`, {headers})
        .then(res => {
            console.log(res);

          })
    }

    const onCommentModify = (event) => {
        if(com !== "") {

            const token = localStorage.getItem('token');
            const headers = {
                "Content-Type" : "application/json",
                Authorization : `Bearer ${token}`,
              };

            let body = {
            board : id,
            text : com,
            }
            console.log(body);
            axios.patch(`http://43.200.205.125:8000/board/comment/${event.target.value}/`, body, {headers})
            .then(res => {
                console.log("?");
                console.log(res);
                window.location.reload();
                window.scrollTo(0,document.body.scrollHeight);

              })

            console.log(com);

        }
    }

    const onCommentDelete = (event) => {
        const token = localStorage.getItem('token');
        const headers = {
            "Content-Type" : "application/json",
            Authorization : `Bearer ${token}`,
          };
        
          
        console.log(123);
        console.log(event.target.value);

        axios.delete(`http://43.200.205.125:8000/board/comment/${event.target.value}/`, {headers})
        .then(res => {
            console.log("?");
            console.log(res);
            window.location.reload();
            window.scrollTo(0,document.body.scrollHeight);

          })
    }

    const onCommenthandler = (event) => {
        console.log(event.target.value);
        setComs(event.target.value);
    }

    const onCommithandler = (event) => {
        if(com !== "") {

            const token = localStorage.getItem('token');
            const headers = {
                "Content-Type" : "application/json",
                Authorization : `Bearer ${token}`,
              };

            

            let body = {
            board : id,
            text : com,
            }
            console.log(body);
            axios.post(`http://43.200.205.125:8000/board/comment/`, body, {headers})
            .then(res => {
                console.log("?");
                console.log(res);
                window.location.reload();
                window.scrollTo(0,document.body.scrollHeight);

              })

            console.log(com);

        }
    }
    console.log(comments)
    useEffect(() => {

        const token = localStorage.getItem('token');
        const headers = {
            "Content-Type" : "application/json",
            Authorization : `Bearer ${token}`,
          };

        axios.get(`http://43.200.205.125:8000/board/${id}`, {headers})
            .then(res => setPosts(res.data))
            .catch(error => console.log(error))
    }, []);
        
    if(Object.keys(posts).length === 10) {
        const keys = Object.keys(posts);
        if(Object.keys(posts[keys[9]]).length > 0) {
            while(comments.length != 0) {
                comments.pop();
            }
            for (let index = 0; index < Object.keys(posts[keys[9]]).length; index++) {
                const comment_keys = Object.keys(posts[keys[9]][index]);

                // author
                const author_keys = Object.keys(posts[keys[9]][index][comment_keys[1]]);
                
                const pk = posts[keys[9]][index][comment_keys[0]];

                // username
                console.log(posts[keys[9]][index][comment_keys[1]][author_keys[1]]);
                const id = posts[keys[9]][index][comment_keys[1]][author_keys[1]]
                // comment
                console.log(posts[keys[9]][index][comment_keys[3]]);
                const comment = posts[keys[9]][index][comment_keys[3]];

                comments.push({
                    pk : pk,
                    id : id,
                    comment : comment,
                })
            }
        }
        console.log(keys);
        console.log(posts[keys[7]]);
        
        return (
            <Wrapper>
            
            <div style={{marginTop: "7%" ,marginLeft: "20%",  flex:"1"}}>
                <div style={{borderBottomStyle:"solid", borderBottomWidth:1, borderColor:"black",width:"80%"}}><b>{ `${posts[keys[3]]})` }</b> {posts[keys[2]]}</div>
                <br/>
                <div style={{borderBottomStyle:"solid", borderBottomWidth:1, borderColor:"black",width:"80%"}}>작성일 : {posts[keys[6]].split("T")[0]}</div>
                <br/>
                <div style={{borderBottomStyle:"solid", borderBottomWidth:1, borderColor:"black",width:"80%"}}>내용 : {posts[keys[4]]}</div>
                <br/>
                
                <div style={{width:"100%"}}><img style={{width:"80%"}}  src={`http://43.200.205.125:8000${posts[keys[5]]}`}/></div>
                <div style={{borderBottomStyle:"solid", borderBottomWidth:1, borderColor:"black",width:"80%"}}></div>

                <div className="button voc-view-row">   
                <div style={{width: "200%"}}>
                <ButtonGroup>
                        <Button variant="outline-secondary" href={`../update/${id}`}>수정하기</Button>
                        <Button variant="outline-secondary" href='../board'>이전으로</Button>
                </ButtonGroup>
                </div> 
                <div style={{float: "right"}}>
                    <span onClick={() => onLikeHandler()}>
                        👍{posts[keys[7]][0]}
                    </span>
                    👁{posts[keys[8]][0]}
                </div>
                </div>
                <div className='Reply_div'>
                    <h4> 댓글 </h4>
                    <div className='Reply_write'>
                    <textarea value={com} rows='3'placeholder='100자 이내의 글을 입력해주세요.'
                        onChange={onCommenthandler} maxLength='100' name='write_reply'>
                    </textarea>
                    <input onClick={onCommithandler} type='button' value='등록' id='reply_submit_button'/>
                    </div>
                </div>
                <br/>
                {
                    comments.map(({pk, id, comment}) => (
                        <div style={{height:"12%",display:"flex", flexDirection: "column" ,borderBottomStyle:"solid", borderBottomWidth:1, borderColor:"black",width:"80%"}}>
                            <div style={{display:"flex", flexDirection: "row"}}>
                                <div style={{width: "93.5%"}}>
                                    <b>{ `${id})` }</b> {comment}
                                </div>
                                <Button variant="outline-secondary" size="sm" style={{float:"right"}} value={pk} onClick={onCommentDelete}>삭제</Button>
                            </div>
                            <div style={{display:"flex", flexDirection: "row"}}>
                                <div style={{width: "93.5%"}}>
                                    <input onChange={onCommenthandler} style={{width: "100%", resize: "none"}}></input>
                                </div>
                                <Button variant="outline-secondary" size="sm" style={{float:"right"}} value={pk} onClick={onCommentModify}>수정</Button>
                            </div>
                        </div>          
                    ))
                }
                <br/>

            </div>
            
            {/* <div style={{flex:"1"}}>                
                <img  src={boardlist[0].image}/>
            </div> */}

        </Wrapper>
    )
    } else {
        return (
            <Wrapper>
            <div style={{position: "fixed", marginTop: "30%", marginLeft: "40%"}}>
                로딩중...
            
            </div>
        </Wrapper>
    )
    }
}

export default BoardItem