import React from "react";
import ProductModal from "./Productmodal";
import './Product.css'
import { useState } from "react";
import axios from "axios";




function ProductItem(props) {
    const [setproduct, setProductOn] = useState(false);

    const onLikeHandler = (pk) => {
        const token = localStorage.getItem('token');
        const headers = {
            "Content-Type" : "application/json",
            Authorization : `Bearer ${token}`,
          };

        axios.get(`http://43.200.205.125:8000/product/like_product/${pk}/`, {headers})
        .then(res => {
            console.log(res);

          })
    }

    const onViewHandler = (pk) => {
        setProductOn(true)
        const token = localStorage.getItem('token');
        const headers = {
            "Content-Type" : "application/json",
            Authorization : `Bearer ${token}`,
          };

        axios.get(`http://43.200.205.125:8000/product/${pk}/`, {headers})
        .then(res => {
            console.log(res);
          })
    }

        return (
            <div className="product">
                <ProductModal pk={props.pk} src={props.src} pname={props.pname} event={props.event} price={props.price} category={props.category} views={props.views} likes={props.likes} show={setproduct} onHide={() => setProductOn(false)}/>
                <div className="product_img_div">
    
                    <a onClick={() => onViewHandler(props.pk)} className='text-decoration-none' style={{ color : 'inherit'}}>
                        <img src={props.src} className="product_img"/>
                    </a>
                </div>
                <h5 className="product_title">{props.pname}</h5>
                <div className="product_mon">{props.price}원</div>
                <div>
                    <b style={{backgroundColor: "pink"}}className="product_des">{props.event}</b>
                    <span
                        onClick={() => onLikeHandler(props.pk)}
                    >
                        👍
                    </span>
                    {props.likes.length}
                    👁{props.views.length}
                </div>
            </div>
        )

}

export default ProductItem