import React from "react";
import './Product.css'
import styled from 'styled-components';
import Paging from "./Paging";
import { useState } from "react";
import ProductItem from "./Productitem";
import { Button, Form } from "react-bootstrap";
import Pagination from "./Pagination";
import { useEffect } from "react";
import axios from "axios";
import './Searchbar.css'

const Wrapper = styled.div`
    /* 레이아웃 */
    display: flex;
    flexDirection: row;
    position: fixed;
    width: 100%;
    height: 100%;
    max-height: 550px;
    margin: 0 auto;
    margin-top: 50px;
    align-items: center;
    justify-content: space-between;
    overflow-y : auto;
`;

const Cnt = styled.div`
    display: flex;
    align-items: center;
    position: inherit;
    width: 100%;
    height: 15%;
    justify-content: center;
    background-color: white;
    left: 7%;
    width: 90%;
    top: 2%;
`;

const SearchBar = styled.div`
    display: flex;
    height: 100%;
    width: 50%;
    // background-color: pink;
    align-items: center;
    justify-content: center;
`

const Sort = styled.div`
    display: flex;
    align-items: center;
    position: inherit;
    width: 100%;
    justify-content: center;
    //background-color: black;
    top: 18%;
`;

const Buttons = styled.div`
  display: flex;
  width: 70%;

  justify-content: space-between;
`

function Search(props) {

    const [posts, setPosts] = useState([]);
    const [postsp, setPostsp] = useState([]);
    const [limit, setLimit] = useState(16);
    const [page, setPage] = useState(1);
    const offset = (page - 1) * limit;
    const [search, setSearch] = useState('');

    const [choice, setChoice] = useState(0);
    const productlist = [
    ];

    const products = JSON.parse(Object.assign(localStorage.getItem('all')));

    const [searchValue, setSearchValue] = useState("");

    const handleInputChange = (event) => {

        // console.log(event.target.value);        
        setPage(1);
        setSearchValue(event.target.value)

    }

    const shouldDisplayButton = searchValue.length > 0;
    const handleInputClear = () => {
        setSearchValue("");
    }


    const filteredProducts = products.filter((product) => {

        return product.pname.includes(searchValue);

    })

    return(
        <Wrapper>
            <Cnt>
                <div class="form__group field">
                <input type="input" class="form__field" placeholder="검색어" name="name" id='name' required value={searchValue} onChange={handleInputChange}/>
                </div>
            </Cnt>
            

            <div className="product_container">

                {filteredProducts.slice(offset, offset + limit).map(({ image, pname, pevent, price, category}) => {
                    return <ProductItem src = {image} pname = {pname} event = {pevent} price={price} category={category}></ProductItem>
                })}

            </div>

                 <div className="page_">
                <Paging
                    total={filteredProducts.length}
                    limit={limit}
                    page={page}
                    setPage={setPage}
                    />
                </div>  

        </Wrapper>

    )

}


export default Search;